@import '../../styles/breakpoints';

.sb-footer {
  background-color: var(--sbt-theme-tertiary-color-70);
  color: var(--sbt-theme-tertiary-color-10);
  font-family: var(--sbt-font-stack-primary);
  font-size: var(--sbt-font-size-15);
  padding: 0.5rem 1rem;
  width: 100%;

  @include window-width-max-small {
    position: relative;
  }

  a {
    color: var(--sbt-theme-tertiary-color-10);
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }
}

.sb-footer_sticky {
  bottom: 0;
  position: fixed;
}

.sb-footer__terms-privacy {
  @include window-width-max-small {
    display: none;
  }
}
